import React from "react";
import style from "./Alerta.css"
import RemindOutlineIcon from '@rsuite/icons/RemindOutline';

export default function Alerta() {
    return (
        <div id="alerta">
            <p>
            <RemindOutlineIcon fontSize={'30px'} color='black'/>
             Os vdeos dessa sesso esto dispostos em seu formato integral, no possuem cortes, excluso de pessoas, ou qualquer edio que tenha por objetivo eliminar parte do contedo, apenas edies feitas com o intuito de tornar os vdeos mais divertidos.
            </p>
        </div>
    )
}